<template>
  <div class="container">
    <div v-if="recipe">
      <div class="recipe-header mb-2 mt-3 mb-4">
        <h1 style="font-weight: bold;">{{ recipe.title }}</h1>
        <img v-if="!isFamilyRecipe" 
         :src="recipe.image" 
         class="center" />
        <img v-else 
         :src="require(`@/assets/${recipe.image}`)" 
         class="center" />
      </div>
      <div class="recipe-body">
        <div class="text-align center ">
            <div class="mb-3 margin-bottom: auto;" style="display: flex;">
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <div style="font-weight: bold;">Ready in:</div>
              &nbsp;&nbsp;
              <p>{{ recipe.readyInMinutes }} minutes</p>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <div style="font-weight: bold;">Likes:</div>
              &nbsp;&nbsp;
              <p> {{ recipe.aggregateLikes }} likes</p>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <div style="font-weight: bold;">Servings:</div>
              &nbsp;&nbsp;
              <p> {{ recipe.servings }}</p>
            </div>
            <div style="display: flex; ">
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <p v-if="recipe.vegetarian" style="margin-right: 10px;"><img src="data:image/png;base64,"></p>
              <p v-if="recipe.vegan" style="margin-right: 10px;"><img src="data:image/png;base64,"></p>
              <p v-if="recipe.glutenFree" style="margin-right: 10px;"><img src="data:image/png;base64," style="width:20px; height:20px"></p>
              <p v-if="!recipe.glutenFree"><img src="data:image/png;base64,"></p>
              <b-button variant="link" class="text-decoration-none mb-2" @click="toggleFav">
                  <img v-if="!isFav" style="width:20px; height:20px" src="data:image/png;base64," alt="Image 1" >
                  <img v-else style="width:20px; height:20px;" src="data:image/png;base64," alt="Image 2">
              </b-button>
            </div> 
          </div>
        <div class="wrapper">
          <div class="wrapped">
          <div style="font-weight: bold;">Ingredients: </div>
            <ul>
              <li
                v-for="(r, index) in recipe.extendedIngredients"
                :key="index + '_' + r.id"
              >
                {{ r.original }}
              </li>
            </ul>
          </div>
          <div class="wrapped">
            <div style="font-weight: bold;">Instructions: </div>
            <ol>
              <li v-for="s in recipe._instructions" :key="s.number">
                {{ s.step }}
              </li>
            </ol>
          </div>
        </div>
      </div>
      <!-- <pre>
      {{ $route.params }}
      {{ recipe }}
    </pre
      > -->
    </div>
  </div>
</template>
<script>
import { mockGetRecipeFullDetails } from "../services/recipes.js";
import { mockAddFavorite } from "../services/user.js";
export default {
  name: "RecipeViewPage",
  data() {
    return {
      recipe: null,
      isFav: "",
      message: ""
    };
  },
  computed:{
    isFamilyRecipe:{
      get(){
        if(this.$route.params.category === "family"){
          return true;
        }
        return false;
      }
    }
  },
  async created() {
    try {
      let response;
      // response = this.$route.params.response;

      try {
        // response = await this.axios.get(
        //   this.$root.store.server_domain + "/recipes/" + this.$route.params.recipeId,
        //   {
        //     withCredentials: true
        //   }
        // );
        // if(this.$route.params.category === "family"){
        //   this.isFamilyRecipe = true;
        // }
        response = mockGetRecipeFullDetails(this.$route.params.recipeId);

        // console.log("response.status", response.status);
        if (response.status !== 200) this.$router.replace("/NotFound");
      } catch (error) {
        console.log("error.response.status", error.response.status);
        this.$router.replace("/NotFound");
        return;
      }

      let {
        analyzedInstructions,
        instructions,
        extendedIngredients,
        aggregateLikes,
        readyInMinutes,
        image,
        title,
        servings,
        vegetarian,
        vegan,
        glutenFree
      } = response.data.recipe;

        // Concatenate name with each step in analyzedInstructions
        let _instructions = analyzedInstructions.flatMap(instruction => {
        let sectionName = instruction.name ? `${instruction.name}` : '';
        return instruction.steps.map(step => ({
          ...step,
          step: `${sectionName}${step.step}`
        }));
      });

      let _recipe = {
        instructions: analyzedInstructions,
        _instructions,
        analyzedInstructions,
        extendedIngredients,
        aggregateLikes,
        readyInMinutes,
        image,
        title,
        servings,
        vegetarian,
        vegan,
        glutenFree
      };

      this.recipe = _recipe;
      this.isFav= localStorage.getItem(`favorite_${this.recipe.id}`) === 'true';
    } catch (error) {
      console.log(error);
    }
  },
  methods: {
  toggleFav(event) {
      event.stopPropagation();
      this.isFav = !this.isFav;
      localStorage.setItem(`favorite_${this.recipe.id}`, this.isFav.toString());
      if (this.isFav){
        if (mockAddFavorite(this.recipe.id).status === 200){
          this.message = mockAddFavorite(this.recipe.id).response.data.message;
        }
      }
      else{
        this.message = "The Recipe successfully removed from favorites"
      }
      this.$root.toast("",this.message, "Light ");
    },
  }
};
</script>
<style scoped>
.container {
  font-family: 'Calibri Light', 'Calibri';
}
.wrapper {
  display: flex;
}
.wrapped {
  width: 50%;
}
.recipe-header {
  text-align: center; /* Ensures the header text is centered */
}
.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}

/* .recipe-header{

} */
</style>
